import React from "react";
import { Dialog } from "@headlessui/react"; //   
import { X } from "lucide-react";

export default function TermsModal({ isOpen, onClose, type }) {
  const content =
    type === "terms"
      ? {
          title: " ",
          content: ` 
            <h3 class="text-lg font-bold mb-2">1. </h3>
            <p class="mb-4">        .        .</p>
            <h3 class="text-lg font-bold mb-2">2.  </h3>
            <p class="mb-4">    .          .    ,        .</p>
            <h3 class="text-lg font-bold mb-2">3. </h3>
            <p class="mb-4">                 .</p>
            <h3 class="text-lg font-bold mb-2">4.  </h3>
            <p class="mb-4">   ,  , ,   ,             .</p>
          `
        }
      : {
          title: " ",
          content: `
            <h3 class="text-lg font-bold mb-2">    </h3>
            <p class="mb-4">          (  ), "-2013.</p>
            <h3 class="text-lg font-bold mb-2">   :</h3>
            <ul class="list-disc pr-5 mb-4">
              <li>   </li>
              <li>   </li>
              <li>    </li>
              <li>   </li>
              <li>  </li>
            </ul>
            <h3 class="text-lg font-bold mb-2">   </h3>
            <p class="mb-4">          ,     054-7260002  .</p>
          `
        };

  return (
    <Dialog open={isOpen} onClose={onClose}>
      <div className="fixed inset-0 z-50 flex items-center justify-center">
        <Dialog.Panel className="bg-white rounded-lg max-w-3xl max-h-[80vh] overflow-y-auto shadow-xl">
          <div className="flex items-center justify-between p-4 bg-gray-100 rounded-t-lg sticky top-0 z-10 bg-white">
            <h2 className="text-xl font-bold">{content.title}</h2>
            <button
              onClick={onClose}
              className="bg-gray-200 rounded-full p-1 text-gray-600 hover:text-gray-800"
            >
              <X className="h-4 w-4" />
            </button>
          </div>
          <div
            className="mt-4 text-right p-4"
            dangerouslySetInnerHTML={{ __html: content.content }}
          />
        </Dialog.Panel>
      </div>
    </Dialog>
  );
}
